// Admin Controller
app.controller('AdminController', function($scope,$http)
{
    $scope.pools = [];
});

// Home Controller
app.controller('MainController', ['dataFactory', '$scope', '$http', function(dataFactory,$scope,$http)
{
    // Settings
    $scope.data = [];
    $scope.statusGet = [];
    $scope.libraryTemp = {};
    $scope.libraryTempStatus = {};
    $scope.totalItemsTemp = {};
    $scope.totalItems = 0;
    $scope.statusSelect = {};

    $scope.pageChanged = function(newPage)
    {
        getResultsPage(newPage);
    };

    getResultsPage(1);

    // Load table
    function getResultsPage(pageNumber)
    {
        if(! $.isEmptyObject($scope.libraryTemp))
        {
            dataFactory.httpRequest('/workflow?search='+$scope.searchText+'&page='+pageNumber).then(function(data) {
                $scope.data = data.data;
                $scope.statusGet = data.data.status;
                $scope.totalItems = data.total;
                $scope.item_num = data.from;
            });
        } else {
            dataFactory.httpRequest('/workflow?page='+pageNumber).then(function(data) {
                $scope.data = data.data;
                $scope.statusGet = data.data.status;
                $scope.totalItems = data.total;
                $scope.item_num = data.from;
            });
        }
    }

    // при поиске менять статусы тоже

    // Search
    $scope.searchDB = function()
    {
        if($scope.searchText.length >= 3)
        {
            if($.isEmptyObject($scope.libraryTemp)){
            $scope.libraryTemp = $scope.data;
            $scope.libraryTempStatus = $scope.statusGet;
            $scope.totalItemsTemp = $scope.totalItems;
            $scope.data = {};
            $scope.statusGet = {};
        }

        getResultsPage(1);

        } else {
            if(! $.isEmptyObject($scope.libraryTemp))
            {
                $scope.data = $scope.libraryTemp;
                $scope.statusGet = $scope.libraryTempStatus;
                $scope.totalItems = $scope.totalItemsTemp;
                $scope.libraryTemp = {};
                $scope.libraryTempStatus = {};
            }
        }
    }

    // Status update
    $scope.statusUpdate = function(type)
    {
        var scope, id;

        scope = $scope.statusSelect;
        id = Object.keys(scope);

        // update ng-selected="s.status_id == value.type"
        var value = id.map(function(key) {
            return scope[key];
        });

        this.value[type] = parseInt(value);

        // add type in status
        scope.type = type;

        // request
        dataFactory.httpRequest('home/'+id,'PUT',{},scope).then(function(data) {
            console.log(data);
        });

        // clear
        $scope.statusSelect = {};
    }

}]);